body {
  background-color: rgba(151, 181, 243, 0.82);
  display: flex;
  justify-content: center;
  align-items: center;
}
body .main-container {
  width: 2048px;
  height: auto;
}
body .main-container .road-one {
  width: 100%;
  height: 1536px;
  background-image: url("../assets/background/Map1.jpg");
  background-repeat: no-repeat;
  background-size: contain;
  display: flex;
  flex-direction: row;
}
body .main-container .road-one .col {
  width: 33.3%;
  display: flex;
  flex-direction: column;
}
body .main-container .road-one .project {
  width: 18.75rem;
  height: 25rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(200, 234, 159, 0.38);
  border-radius: 7rem;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-left: 1px solid rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(3px);
}
body .main-container .road-one .project .project-snippet {
  height: 170px;
  width: 200px;
  border-top-left-radius: 7rem;
  border-top-right-radius: 7rem;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: 0.5s;
  filter: blur(3px);
  -webkit-filter: blur(3px);
}
body .main-container .road-one .project .project-snippet:hover {
  width: 18.75rem;
  height: 25rem;
  border-radius: 50%;
  border-top-left-radius: 7rem;
  border-top-right-radius: 7rem;
  filter: none;
  -webkit-filter: none;
  cursor: pointer;
}
body .main-container .road-one .project .project-title {
